@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: 'Barlow Semi Condensed', sans-serif;

  display:flex;
  flex-direction:column;
  align-items:center;
  background: radial-gradient(hsl(214, 47%, 23%) ,hsl(237, 49%, 15%));
  background-size: cover;
  height: 100vh;
}




.App {
  text-align: center;
  height: 100vh;
  display:flex;
  flex-direction:column;
  align-items:center;
  width:100%;
  overflow:hidden;
  
}

.blur {
    filter:blur(10px);
}

.ch {
  margin: 6em 0em 0em;
  display: flex;
  justify-content: center;
  width: 65%;
  flex-grow:1;
  height: 300px;
}

.res {
  display: flex;
  justify-content: center;
  width: 65%;
  flex-grow:1;
}

@media screen and (max-width: 1150px) {
  .result-container{
    transform:scale(.9) !important;
  }
  .result-show{
    order:3;
  }
  .header-container {
    width:80% !important;
    transform:scale(1) !important;
    
  }

  .player, .opponent {
    /* transform:scale(.6) !important; */
    width:50% !important;
    justify-content:center !important;
  }
}

@media screen and (max-width: 900px) {
  .player, .opponent {
    transform:scale(.8) !important;
    width:50% !important;
  }

  .player-before, .opponent-before {
    transform: scale(.7) !important;
    margin: 0 !important;
  }

  .chosen-opponent {
    justify-content: center !important;
  }

  .result {
    order:3;
  }
}



@media screen and (max-width: 575px) {
  .res{
    width: 85%;
  }

  .ch {
    margin:0;
  }
  .result-show{
    order:3;
    padding:0 !important;
    transform:scale(1.5) !important;
  }
  .result {
    width:100% !important; 
    order: 3;
  }
  .player, .opponent {
    transform:scale(.7) !important;
    justify-content:center !important;
    align-items:center !important;
    /* width:50% !important; */
  }
  .picked-player, .picked-opponent {
    order:2;
  }

  /* .shadow {
    margin-bottom: 4em !important;
    margin-bottom: 1.5em;
  } */

  .choice {
    transform: scale(.6) !important;
  }

  .win-player, .win-opponent {
    margin-bottom: 8em !important;
    z-index:-1 !important;

  }
  .player-before, .opponent-before {
    transform:scale(.7) !important;
    width:50%;

  }

  footer {
    justify-content: center !important;
    padding-bottom: 2em !important;
  }

  .rules-btn {
    transform:scale(1.3) !important;
  }

  .header-container {
    width:110% !important;
    transform:scale(.55) !important;
    
  }
}
* {
    box-sizing: border-box;
}

.main-header {
    margin:auto;
    width: 100%;
    display:flex;
    align-items:center;
    justify-content: center;
    padding: 1em 1.5em;

  }

.header-container {
    border: solid hsl(217, 16%, 45%);
    width:50%;
    border-radius: 1em;
    display:flex;
    align-items: center;
    justify-content:space-between;
    padding: 1em 1.5em;

    
}

.header__logo {
    padding: 0;
}

.header__score-container {
    height: 100%;
    width: 15%;
    display:flex;
    flex-direction:column;
    justify-content: center;
    align-items:center;
    background-color: white;
    border-radius: .5em;
    padding: 1em 3.5em;

}

.header__score {
    margin: 0;
    color: hsl(229, 25%, 31%);
    font-size: 3em;
    font-weight: 700;
}

small {
    letter-spacing: .1em;;
    color: hsl(229, 64%, 46%);
}


.rules-btn {
    margin:2em;
    background:transparent;
    border: solid .5px white;
    outline: none;
    padding: .8em 2.5em;
    border-radius: .5em;
    color: white;
    font-size: 9px;
    line-height: 8px;
    font-weight: 700;
    letter-spacing: .1em;
    cursor: pointer;
}

.rules-btn:hover {
    transition:all .2s ease-in;
    background: white;
    color:hsl(237, 49%, 15%);
}

footer {
    display:flex;
    justify-content: flex-end;
    width: 100vw;
}


.choice {
    background: url(/rock-paper-scissors/static/media/bg-triangle.c0c30b56.svg) no-repeat;
    background-position: center 5em;
    background-size:25em;
    display:flex;
    height: 450px;
    transform:scale(.95);
}

.choice-bg {
    pointer-events: none;
    width: 10.5em;
    height: 10.5em;
    border-radius: 50%;
    box-shadow: inset 0em .5em .5em 3px rgb(202, 202, 202);
    background: white;
    background-size: 45%;
    display:flex;
    justify-content:center;
    align-items:center;
}

.rock, .paper, .scissors {
    display:flex;
    justify-content: center;
    align-items:center;
    width: 13em;
    height: 13em;
    border-radius:50%;
    transition:all .2s ease-out;

    
}

.paper {
    position: relative;
    top:-1em;
    left: 3em;
    background: linear-gradient(hsl(230, 89%, 62%),hsl(230, 89%, 65%));
    box-shadow: 0px 8px 0px .5px hsl(230, 54%, 38%);
    
}

.scissors {
    position: relative;
    top:-1em;
    right: -11em;
    background: linear-gradient(hsl(39, 89%, 49%), hsl(40, 84%, 53%));
    box-shadow: 0px 8px 0px .5px hsl(40, 77%, 28%);
}

.rock {
    position: relative;
    align-self:flex-end;
    right: 13em;
    background: linear-gradient(hsl(349, 71%, 52%) , hsl(349, 70%, 56%));
    box-shadow: 0px 8px 0px .5px hsl(349, 55%, 31%);
}

.icon {
    transform: scale(1.5);
}

.scissors:hover, .paper:hover, .rock:hover {
    transform:scale(1.2);
    transition:all .2s ease-out;
}


.result-container {
    display:flex;
    flex-wrap:wrap;
    align-items: center;
    justify-content:center;
    height:100%;
    width:100%;
   transform: scale(1.2);
    
}

.player, .opponent {
    position:relative;
   display:flex;
   flex-direction:column;
   justify-content: center;
   align-items: center;
   margin:auto;
   grid-gap:2em;
   gap:2em;
   transform:scale(1.2);
}

.player-before, .opponent-before {
    position:relative;
    display:flex;
    flex-direction:column;
    justify-content: center;
    align-items: center;
    /* margin:auto; */
    grid-gap:2em;
    gap:2em;
    transform:scale(1.2);
}

.player-before {
    margin-right: 4em;
}


.opponent-before {
    grid-gap: 3.5em;
    gap: 3.5em;
    margin-left: 4em;
}

.win {
    display:none;
}

.win-player, .win-opponent {
    background: rgba(97, 97, 97, 0.1);
    position:absolute;
    width: 30em;
    height:30em;
    border-radius:50%;
    z-index:-1;
    animation:grow .5s;
    margin-top:4em;
}

.middle {
    background: rgba(97, 97, 97, 0.1);
    position:absolute;
    width: 25em;
    height:25em;
    border-radius:50%;
    top: 2.5em;
    left: 2.5em;
}

.inner {
    background: rgba(97, 97, 97, 0.1);
    position:absolute;
    width: 20em;
    height:20em;
    border-radius:50%;
    top: 5em;
    left: 5em;
}

p {
    margin: 0;
}

.picked-opponent {
    color:white;
    font-weight: 800;
    font-size: 1.33em;
    letter-spacing: .05em;
}
.picked-player {
    color:white;
    font-weight: 800;
    font-size: 1.33em;
    letter-spacing: .05em;
}

.chosen-player {
    position:unset;
}

.chosen-opponent {
    position:unset;

}

.shadow {
    border-radius: 50%;
    background: rgba(19, 18, 18, 0.425);
    height: 10em;
    width: 10em;
    transform: scale(1.1);
    display:flex;
    justify-content:center;
    align-items:center;
    margin-bottom:1.5em;
}

span {
    position:relative;
    animation:pulse;
    animation-duration: 1s;
    animation-iteration-count: 3;
    color: white;
    font-size:5em;
}

.result-show {
    padding-top:4em;
    display:flex;
    flex-direction: column;
    justify-content:center;
    transform:scale(1.2);
    z-index:1;
}

.result {
    display:none;
}
.result-label{
    margin:0;
    padding-bottom: .25em;
    font-size: 36px;
    letter-spacing:0em;
    font-weight: 800;
    color:white;
}

.reset {
    background:white;
    outline: none;
    border:none;
    padding: .8em 4.5em;
    border-radius: .67em;
    color:hsl(237, 49%, 15%);
    /* width: 15em; */
    font-size: 9px;
    line-height: 12px;
    font-weight: 500;
    letter-spacing: .2em;
    cursor: pointer;
    height: 3.5em;
}

.reset:hover {
    color:hsl(349, 71%, 52%);
    font-weight: 700;
    padding: .79em 4.38em;
    transition: color .2s ease-in;



}
@keyframes pulse{
    0%    { scale: 1; }
    20%   { scale: 1.2; }
    100%  { scale: 1; }
  }


@keyframes grow {
    0%    { scale: 0; }
    20%   { scale: .5; }
    100%  { scale: 1; }
}


.rules {
    background:white;
    border-radius: 1em;
    position:absolute;
    z-index:1;
    display:flex;
    justify-content:center;
    align-items: center;
    flex-direction:column;
    padding: 1em 2em;
    top: 25%;
    grid-gap:1.5em;
    gap:1.5em;
    box-shadow: 0em .3em .1em gray;
}



.rules-header {
    display:flex;
    justify-content: space-between;
    align-items:center;
    width: 100%;


}

.rules-header > button {
    background:none;
    border:none;
}

.rules > img {
    margin-bottom: 1em;
    width:90%;
}

.rules-header > button:hover {
    transition:all .2s ease-in;
    transform:scale(1.2);
}
